@media only screen and (max-width: 900px) {
    h2 {
    margin-top: 0.5rem;
    margin-bottom: 0px;
    }
    #title {
        margin-left: 0.5rem;
        font-size: 1.0rem;
    }
    #reader {
        width: 100%;
    }
    #title, #searchContents, #checkOutInput, #checkOutResult, #checkOutUser, #noticeList #noticeContent {
        width: 100%;
        margin-left: 0em;
        margin-right: 0em;
        font-size: 1.8rem;
    }
    #search, #checkOut, #situation, #notice {
        padding: 0px;
    }
    #hkPrefix {
        font-size: 1.8rem;
    }
    #searchInput, #searchPassword, #logIn, #logOutButton, #confirm, #cancel, #checkRent, #checkReturn {
        width: 100%;
        font-size: 1.8rem;
        border: 2px solid gray;
    }
    #userInfo {
        align-items: center;
        display: flex;
        flex-direction: column;
        padding: 0px;
        margin: 0px;
    }
    #userItem {
        width: 100%;
        font-size: 1.8rem;
        padding: 5px;
    }
    #bookList {
        width: 100%;
    }

    #bookEntry, #dueDate {
        width: 100%;
        flex-direction: column;
        display: flex;
        font-size:1.5rem;
        padding: 1rem;
    }

    #bookItem {
        padding-left: 1rem;
        padding-right: 1rem;
    }

    .idPrefix, #AA, #AB {
        padding: 1rem;
        margin-bottom: 1rem;
        font-size: 1.8rem;
    }
}
